import { mapGetters } from 'vuex'

export default {
  name: 'SubscriptForm',
  data () {
    return {
      email: '',
      dismissSecs: 5,
      dismissCountDown: 0
    }
  },
  computed: {
    ...mapGetters({
      isSuccess: 'blog/getIsSuccessEmail'
    })
  },
  methods: {
    emailSubmit () {
      this.$store
        .dispatch('blog/insertEmail', {
          email: this.email
        })
        .then(() => {
          if (this.isSuccess) {
            this.showAlert()
            this.email = ''
          }
        })
    },
    countDownChanged (dismissCountDown) {
      this.dismissCountDown = dismissCountDown
    },
    showAlert () {
      this.dismissCountDown = this.dismissSecs
    }
  }
}
